angular.module('festima')
  .service('dealers', function($http, appConfig) {
    angular.extend(this, {
      
      findAllByQuery: function(q) {
        return $http.get(appConfig.apiUrl + '/dealers', {params: {q: q}}).then(function(resp) { return resp.data; });
      },
      
      listAll: function() {
        return $http.get(appConfig.apiUrl + '/dealers').then(function(response) { return response.data; });
      },
      
      mapAll: function() {
        return this.listAll().then(function(dealerList) {
          var dealersMap = {};
          var dealer;
          
          for (var i = 0; i < dealerList.length; i++) {
            dealer = dealerList[i];
            if (angular.isUndefined(dealersMap[dealer.id])) {
              dealersMap[dealer.id] = dealer;
            }
          }          
          
          return dealersMap;
        });
      },

      listByIds: function(ids) {
        var params = '?';

        for(var i=0; i<ids.length; i++) {
          params += '&id=' + ids[i];
        }

        return $http.get(appConfig.apiUrl + '/dealers' + params);
      }
    });
});

angular.module('festima')
  .factory('buildingsManager', ['$http', '$q', 'appConfig', 'Building', 'usersManager', 'dictionaries', function($http, $q, appConfig, Building, usersManager, dictionaries) {
  var buildingsManager = {
    _pool: {},
    _retrieveInstance: function(id, data) {
      var instance = this._pool[id];

      if (instance) {
        instance.setData(data);
      } else {
        instance = new Building(data);
        this._pool[id] = instance;
      }

      return instance;
    },
    _search: function(id) {
      return this._pool[id];
    },
    _load: function(id, deferred) {
      var scope = this;

      $http.get(appConfig.apiUrl + '/buildings/' + id)
        .success(function(data) {
          var itemData = data;
          var item = scope._retrieveInstance(itemData.id, itemData);

          deferred.resolve(item);
        })
        .error(function() {
          deferred.reject();
        });
    },

    getBuilding: function(id) {
      var deferred = $q.defer();
      var item = this._search(id);
      if (item) {
        deferred.resolve(item);
      } else {
        if (id === undefined) {
          deferred.resolve(this._retrieveInstance(null));
        } else {
          this._load(id, deferred);
        }
      }
      return deferred.promise;
    },

    loadAllBuildings: function() {
      var deferred = $q.defer();
      var scope = this;
      $http.get(appConfig.apiUrl + '/buildings')
        .success(function(itemsArray) {
          var items = [];
          var item;

            if (itemsArray.length == 0) {
              deferred.resolve([]);
              return;
            }

            itemsArray.forEach(function (itemData) {
              item = scope._retrieveInstance(itemData.id, itemData);
              items.push(item);
              deferred.resolve(items);
            });
        })
        .error(function() {
          deferred.reject();
        });
      return deferred.promise;
    },

    setBuilding: function(data) {
      var scope = this;
      var item = this._search(data.id);
      if (item) {
        item.setData(data);
      } else {
        item = scope._retrieveInstance(data);
      }
      return item;
    }
  };

  return buildingsManager;
}]);

angular.module('festima')
  .factory('buildingDealersManager', ['$http', '$q', 'BuildingDealer', 'dealers', function ($http, $q, BuildingDealer, dealers) {

    var buildingDealersManager = {
      _pool: {},
      _retrieveInstance: function (id, data) {
        var instance = this._pool[id];

        if (instance) {
          instance.setData(data);
        } else {
          instance = new BuildingDealer(data);
          if (id) {
            this._pool[id] = instance;
          } else {
            var unsavedId = Math.min(-1, Math.min(Object.keys(this._pool)));
            instance.id = unsavedId;
            this._pool[unsavedId] = instance;
          }

        }

        return instance;
      },
      _unsaved: function() {
        var unsaved = [];
        var scope = this;

        Object.keys(this._pool).forEach(function(id) {
          if (Number(id) < 0) {
            unsaved.push(scope._pool[id]);
          }
        });

        return unsaved;
      },
      _search: function (id) {
        return this._pool[id];
      },
      _load: function (id, deferred) {
        var scope = this;

        $http.get('http://localhost:3000/api/buildingDealers/' + id)
          .success(function (data) {
            var item = scope._retrieveInstance(data.id, data);

            $http.get('http://localhost:3000/api/dealers/' + data.dealerId).then(
              function (response) {
                item.dealer = response.data;
              }
            );

            deferred.resolve(item);
          })
          .error(function () {
            deferred.reject();
          });
      },

      getBuildingDealer: function (id) {
        var deferred = $q.defer();
        var item = this._search(id);
        if (item) {
          deferred.resolve(item);
        } else {
          this._load(id, deferred);
        }
        return deferred.promise;
      },
      loadAllBuildingDealers: function (id) {
        var deferred = $q.defer();
        var scope = this;
        $http.get('http://localhost:3000/api/buildingDealers', {params: {buildingId: id}})
          .success(function (itemsArray) {
            var items = [];
            var map = {};
            itemsArray.forEach(function (itemData) {
              var item = scope._retrieveInstance(itemData.id, itemData);
              items.push(item);
              map[item.dealerId] = item;
            });

            return dealers.listByIds(Object.keys(map)).then(
              function (response) {
                var list = response.data;

                list.forEach(function (item) {
                  map[item.id].dealer = item;
                });

                deferred.resolve(items);
              }
            )
          })
          .error(function () {
            deferred.reject();
          });
        return deferred.promise;
      },
      setBuildingDealer: function (data) {
        var scope = this;
        var item = this._search(data.id);
        if (item) {
          item.setData(data);
        } else {
          item = scope._retrieveInstance(null, data);
        }
        return item;
      }
    };

    return buildingDealersManager;
  }]);

